

import Http from "./http";

class SearchHttp extends Http {


    //  

    getAutoList = async (keyword,page,allText) => {
        try {
            return await this.axios.get(`/main/search/list?keyword=${keyword}&${page ? `page=${page}` :allText}`)
        } catch (err) {
            throw  err;
        }
    }


    // 
    getAutoSearch = async (text) => {
        try{
            return await  this.axios.get(`/main/auto/search/list?keyword=${text}`)
        }
        catch (err) {
            throw  err;
        }
    }
    getAutoSearchTag = async (text) => {
        try{
            return await  this.axios.get(`/main/auto/tag/search/list?keyword=${text}`)
        }
        catch (err) {
            throw  err;
        }
    }

        getAutoTag= async (text,page,allText) => {
        try{
            return await  this.axios.get(`/main/search/tag/list?keyword=${text}&${page ? `page=${page}` :allText}`)
        }
        catch (err) {
            throw  err;
        }
    }
    //   
    getTopTag = async () => {
        try{
            return await  this.axios.get('/main/top10/tag/list')
        }
        catch (err) {
            throw  err;
        }
    }
     // 10 
    getTopList = async () => {
        try{
                return await  this.axios.get('/main/top10/search/list')
        }
        catch (err) {
            throw  err;
        }
    }


}
export default SearchHttp;
